import { Component, Input } from '@angular/core';
import { NgbActiveModal } from '@ng-bootstrap/ng-bootstrap';
import { IBulkOperationItem, IBulkOperationResult } from '@shared/models';

@Component({
  selector: 'j-multiple-sign-partial-error',
  templateUrl: './multiple-sign-partial-error.component.html',
  styleUrls: ['./multiple-sign-partial-error.component.scss'],
})
export class MultipleSignPartialErrorComponent {
  @Input() signResult: IBulkOperationResult;

  constructor(public modal: NgbActiveModal) {}

  dismissModal() {
    this.modal.dismiss();
  }

  closeModal() {
    this.modal.close();
  }

  retrieveErrors(transaction: IBulkOperationItem) {
    let mappedErrors;

    if (transaction.errorData) {
      const errors = Object.entries(transaction.errorData);
      mappedErrors = errors.map((item) => {
        return item[1];
      });
    } else if (transaction.errorMsg) {
      mappedErrors = [transaction.errorMsg];
    } else {
      mappedErrors = [];
    }

    return mappedErrors;
  }

  get failedText() {
    switch (this.signResult && this.signResult.numberOfFailed % 20) {
      case 1:
        return 'HISTORY.PARTIAL.FAILED.ONE';
      case 2:
      case 3:
      case 4:
        return 'HISTORY.PARTIAL.FAILED.FEW';
      default:
        return 'HISTORY.PARTIAL.FAILED.OTHER';
    }
  }

  get failedParams() {
    return {
      transactionsCount: this.signResult && this.signResult.numberOfFailed,
    };
  }

  get failedTransactions() {
    return this.signResult?.results;
  }
}
