import { Component, Input } from '@angular/core';

import { NgbActiveModal } from '@ng-bootstrap/ng-bootstrap';

@Component({
  selector: 'j-delete-partial',
  templateUrl: './delete-partial.component.html',
  styleUrls: ['./delete-partial.component.scss'],
})
export class DeletePartialComponent {
  @Input()
  failedCount: number;
  @Input()
  successCount: number;

  constructor(public modal: NgbActiveModal) {}

  dismissModal() {
    this.modal.dismiss();
  }

  closeModal() {
    this.modal.close();
  }

  get failedText() {
    switch (this.failedCount % 20) {
      case 1:
        return 'HISTORY.DELETE.FAILED.ONE';
      case 2:
      case 3:
      case 4:
        return 'HISTORY.DELETE.FAILED.FEW';
      default:
        return 'HISTORY.DELETE.FAILED.OTHER';
    }
  }

  get failedParams() {
    return {
      transactionsCount: this.failedCount,
    };
  }

  get successText() {
    switch (this.successCount % 10) {
      case 1:
        return 'HISTORY.DELETE.SUCCESS.ONE';
      case 2:
      case 3:
      case 4:
        return 'HISTORY.DELETE.SUCCESS.FEW';
      default:
        return 'HISTORY.DELETE.SUCCESS.OTHER';
    }
  }

  get successParams() {
    return {
      transactionsCount: this.successCount,
    };
  }
}
