import { Component, Input, OnInit } from '@angular/core';
import { NgbActiveModal } from '@ng-bootstrap/ng-bootstrap';
import { IMoney } from '@shared/currency/currency.model';
import { IBulkOperationResult } from '@shared/models';

@Component({
  selector: 'j-multiple-sign-success',
  templateUrl: './multiple-sign-success.component.html',
  styleUrls: ['./multiple-sign-success.component.scss'],
})
export class MultipleSignSuccessComponent implements OnInit {
  @Input() result: IBulkOperationResult;

  groups: {
    money: IMoney;
    count: number;
  }[];

  constructor(public modal: NgbActiveModal) {}

  ngOnInit() {
    this.generateGroups();
  }

  dismissModal() {
    this.modal.dismiss();
  }

  closeModal() {
    this.modal.close();
  }

  mapCountLabel(count: number) {
    switch (count % 20) {
      case 1:
        return 'HISTORY.OPERATION.ONE';
      case 2:
      case 3:
      case 4:
        return 'HISTORY.OPERATION.FEW';
      default:
        return 'HISTORY.OPERATION.OTHER';
    }
  }

  private generateGroups() {
    let groups = {};

    this.result.results.forEach((item) => {
      const itemBalance = item.result.amount;

      if (groups[itemBalance.currency]) {
        groups[itemBalance.currency].money.amount += itemBalance.amount;
        groups[itemBalance.currency].count++;
      } else {
        groups[itemBalance.currency] = {
          money: {
            currency: itemBalance.currency,
            amount: itemBalance.amount,
          },
          count: 1,
        };
      }
    });

    this.groups = Object.values(groups);
  }
}
