import { Component, OnDestroy } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { DatePipe } from '@angular/common';

import { combineLatest, Subscription, throwError, merge } from 'rxjs';
import { catchError, finalize, switchMap, take, tap } from 'rxjs/operators';

import { NgbModal, NgbModalOptions } from '@ng-bootstrap/ng-bootstrap';
import { NotifierService } from 'angular-notifier';

import { SMS_HEADERS } from '@api/api.service';

import { FILTER_MODE, FilterService } from '@filter/index';
import { IFilterScheme, FILTER_PAYMENT_STATUS } from '@filter/filter.model';

import {
  IStatement,
  ITransactionGroup,
  StatementService,
  Transaction,
} from '@statement/index';

import { CompanyService } from '@company/company.service';

import {
  BULK_OPERATION_STATUS,
  IBulkOperationResult,
  IHistoryPayment,
  ISmsToken,
  PAYMENT_CATEGORY,
  PAYMENT_STATUS,
  TOKEN_TYPE,
} from '@shared/models';
import { CURRENCY, IMoney } from '@shared/currency/currency.model';
import { MaskedService } from '@shared/masked/masked.service';
import { FilesService } from '@shared/services/files.service';
import {
  CONFIRM_SIGNATURE_TYPE,
  ConfirmSmsModalComponent,
} from '@shared/components';

import {
  DeleteErrorComponent,
  DeletePartialComponent,
  DeleteSuccessComponent,
  MultipleSignErrorComponent,
  MultipleSignPartialErrorComponent,
  MultipleSignPartialSuccessComponent,
  MultipleSignSuccessComponent,
  SendToSignedErrorComponent,
  SendToSignedPartialComponent,
  SendToSignedPartialErrorComponent,
  SendToSignedSuccessComponent,
  ValidationErrorComponent,
} from '../modals';

import { HistoryService, MAX_PAGE_SIZE_FOR_SIGN } from '../history.service';

const PAGE_START = 1;
const PAGE_SIZE = 25;
const PAGE_SIZE_FOR_SIGN = MAX_PAGE_SIZE_FOR_SIGN;

interface ISignModalParams {
  transactionIds: string[];
  transactionsCount: number;
  totalBalance: IMoney;
}

@Component({
  selector: 'j-history-list',
  templateUrl: './history-list.component.html',
  styleUrls: ['./history-list.component.scss'],
})
export class HistoryListComponent implements OnDestroy {
  filterScheme: IFilterScheme = {
    periodTo: null,
    periodFrom: null,
  };
  filterCurrency = CURRENCY.KZT;
  filterMode = FILTER_MODE.HISTORY;

  modalConfigSmall: NgbModalOptions = {
    backdropClass: 'backdrop_light',
    windowClass: 'modal_light modal_sm',
    backdrop: 'static',
    centered: true,
  };

  modalConfigMedium: NgbModalOptions = {
    backdropClass: 'backdrop_light',
    windowClass: 'modal_light modal_md',
    backdrop: 'static',
    centered: true,
  };

  historyTypes = [
    {
      label: 'HISTORY.ALL',
      url: '/history/all',
    },
    {
      label: 'HISTORY.TENGE_PAYMENTS',
      url: '/history/tenge',
    },
    {
      label: 'HISTORY.CURRENCY_PAYMENTS',
      url: '/history/foreign',
    },
    {
      label: 'HISTORY.CONVERSION',
      url: '/history/conversion',
    },
  ];

  private subscription = new Subscription();

  private pageNumber = PAGE_START;
  private pageSize = PAGE_SIZE;
  private pageSizeForSign = PAGE_SIZE_FOR_SIGN;

  private paymentCategory: PAYMENT_CATEGORY;

  hasMorePages = true;
  errorText: string;

  statement: IStatement;

  isLoading = true;
  isLoadingExtra = true;
  isReady = false;

  hasSignableSelected = false;

  // Checkboxes
  isAllSelected = false;

  selectedTransactions: Transaction[] = [];
  selectedBalance: IMoney;
  selectedCount = 0;

  SESSION_ROUTE_KEY = 'historyRouteQuery';

  private cashedRouteQuery = JSON.parse(
    sessionStorage.getItem(this.SESSION_ROUTE_KEY)
  );

  private canBeNavigatedToForSign: boolean = true;

  constructor(
    private company: CompanyService,
    private filterService: FilterService,
    private historyService: HistoryService,
    private route: ActivatedRoute,
    private router: Router,
    private datePipe: DatePipe,
    private modalService: NgbModal,
    private filesService: FilesService,
    private maskedService: MaskedService,
    private statementService: StatementService,
    private notifierService: NotifierService
  ) {
    const routeSubscription = combineLatest([
      this.route.queryParams,
      this.route.data,
    ])
      .pipe(take(1))
      .subscribe(([queryParams, dataParams]) => {
        if (Object.keys(queryParams).length > 0) {
          this.canBeNavigatedToForSign = false;
        }

        this.paymentCategory = dataParams.category;

        if (this.cashedRouteQuery && !queryParams) {
          queryParams = this.cashedRouteQuery;
        }

        this.updateCashedRouteQuery(queryParams);
        const mappedScheme = this.mapQueryParams(queryParams);

        this.filterScheme = mappedScheme;
        this.filterService.updateExtra(this.filterScheme);
      });

    this.subscription.add(routeSubscription);

    const company$ = this.company.refresh$.pipe(
      tap(() => this.historyService.updateStatusCounts(this.paymentCategory))
    );

    const filter$ = this.filterService.getScheme();

    const changesSubscription = merge(filter$, company$)
      .pipe(
        switchMap((data) =>
          this.init(!Array.isArray(data) ? data : this.filterScheme)
        )
      )
      .subscribe(() => {});

    const importSubscription = this.filesService.onImport().subscribe(() => {
      this.init(this.filterScheme).toPromise();
      this.historyService.updateStatusCounts(this.paymentCategory);
    });

    const filterStatusesSubscription = this.filterService
      .getStatuses()
      .subscribe((statuses) => {
        const forSign = statuses.find(
          (status) => status.value == FILTER_PAYMENT_STATUS.FOR_SIGN
        );

        if (
          forSign.count > 0 &&
          forSign.classList.includes('badge-primary') &&
          this.canBeNavigatedToForSign
        ) {
          this.canBeNavigatedToForSign = false;

          const updatedScheme = Object.assign(this.filterScheme, {
            paymentStatus: FILTER_PAYMENT_STATUS.FOR_SIGN,
          });

          this.filterService.updateExtra(updatedScheme);
        }
      });

    this.subscription.add(changesSubscription);
    this.subscription.add(importSubscription);
    this.subscription.add(filterStatusesSubscription);
  }

  init(
    scheme: IFilterScheme = this.filterService.getDefaults(this.filterMode)
  ) {
    this.filterScheme = Object.assign({}, scheme);

    this.pageNumber = 1;
    this.hasMorePages = false; //        false       response-
    this.isLoading = true;

    this.updateQueryParams();
    return this.loadStatement().pipe(tap(() => this.updateSelectable()));
  }

  ngOnDestroy(): void {
    this.subscription.unsubscribe();
  }

  onScroll() {
    if (this.hasMorePages) {
      this.pageNumber++;
      this.loadStatement().toPromise();
    }
  }

  async validateTransactionsByPaymentId(transactionIds) {
    try {
      const response =
        await this.historyService.validateTransactionsByPaymentId(
          this.company.currentId,
          transactionIds
        );
      if (response.status !== BULK_OPERATION_STATUS.SUCCESS) {
        this.showValidationError(response);
      }
      return response.status;
    } catch (error) {
      this.showValidationError(error);
      return BULK_OPERATION_STATUS.FAILED;
    }
  }

  async onSign(transaction: Transaction) {
    const modalParams: ISignModalParams = {
      transactionIds: [transaction.id],
      totalBalance: transaction.balance,
      transactionsCount: 1,
    };

    try {
      if (transaction.paymentStatus.code === PAYMENT_STATUS.DRAFT) {
        const validationStatus = await this.validateTransactionsByPaymentId(
          modalParams.transactionIds
        );
        if (validationStatus === BULK_OPERATION_STATUS.FAILED) {
          return;
        }
      }
      await this.showSignModal(modalParams);
      this.historyService.updateForSignCount(null, this.paymentCategory);
    } catch (error) {}
  }

  async onSentForSign(transaction: Transaction) {
    this.sendToSigned([transaction]);
  }

  onSelect() {
    this.updateSelectable();
  }

  selectAll() {
    this.currentTransactions.forEach((transaction) => {
      transaction.isSelected = this.isAllSelected;
    });
    this.updateSelectable();
    this.isAllSelected = true;
  }

  selectSignable() {
    this.currentTransactions.forEach((transaction) => {
      if (transaction.isSignable) {
        transaction.isSelected = this.hasSignableSelected;
      } else {
        transaction.isSelected = false;
      }
    });
    this.updateSelectable();
    this.hasSignableSelected = true;
  }

  get isAllSignable() {
    return (
      this.currentTransactions.length > 0 &&
      (this.filterScheme.paymentStatus === FILTER_PAYMENT_STATUS.FOR_SIGN ||
        this.filterScheme.paymentStatus === FILTER_PAYMENT_STATUS.DRAFT) &&
      this.hasSignableInList(this.currentTransactions)
    );
  }

  get isAllSendableForSign() {
    return (
      !this.isAllSignable &&
      this.currentTransactions.length > 0 &&
      this.filterScheme.paymentStatus === FILTER_PAYMENT_STATUS.DRAFT &&
      this.hasSendableForSignInList(this.currentTransactions)
    );
  }

  get isSignable() {
    return (
      this.currentTransactions &&
      (this.filterScheme.paymentStatus === FILTER_PAYMENT_STATUS.FOR_SIGN ||
        this.filterScheme.paymentStatus === FILTER_PAYMENT_STATUS.DRAFT) &&
      this.isAllSignableInList(this.selectedTransactions)
    );
  }

  get isSendableForSign() {
    return (
      !this.isSignable &&
      this.currentTransactions &&
      this.filterScheme.paymentStatus === FILTER_PAYMENT_STATUS.DRAFT &&
      this.isAllSendableForSignInList(this.selectedTransactions)
    );
  }

  get isSelectable() {
    return (
      this.currentTransactions &&
      (this.filterScheme.paymentStatus === FILTER_PAYMENT_STATUS.FOR_SIGN ||
        this.filterScheme.paymentStatus === FILTER_PAYMENT_STATUS.DRAFT)
    );
  }

  get isSelectableForExport() {
    return (
      this.filterScheme.paymentStatus === FILTER_PAYMENT_STATUS.SUCCESS ||
      this.filterScheme.paymentStatus === FILTER_PAYMENT_STATUS.FAILED ||
      this.filterScheme.paymentStatus === FILTER_PAYMENT_STATUS.IN_PROGRESS ||
      this.filterScheme.paymentStatus === FILTER_PAYMENT_STATUS.DRAFT
    );
  }

  get isAllSelectableForExport() {
    return (
      this.filterScheme.paymentStatus === FILTER_PAYMENT_STATUS.IN_PROGRESS
    );
  }

  get totalBalance() {
    if (this.selectedCount > 0) {
      return this.selectedBalance;
    } else {
      return this.countTotalBalanceFromTransactionsList(
        this.currentTransactions
      );
    }
  }

  get totalCount() {
    if (this.selectedCount > 0) {
      return this.selectedCount;
    } else {
      return this.currentTransactionsCount;
    }
  }

  get currentTransactionsCount() {
    return this.statement?.totalTransactionsCount || 0;
  }

  get currentTransactions() {
    const totalTransactions = this.statement?.transactionGroups?.map(
      (group) => {
        return group.transactions;
      }
    );

    return totalTransactions?.flat() || [];
  }

  get hasAnySelected() {
    return this.selectedCount > 0;
  }

  get currencies() {
    const currencies = {};
    let transactions =
      this.selectedCount > 0
        ? this.selectedTransactions
        : this.currentTransactions;

    transactions.forEach((transaction) => {
      if (transaction.balance?.currency) {
        if (currencies[transaction.balance?.currency]) {
          currencies[transaction.balance?.currency].count++;
          currencies[transaction.balance?.currency].balance.amount +=
            transaction.balance?.amount;
        } else {
          currencies[transaction.balance?.currency] = {
            count: 1,
            balance: {
              amount: transaction.balance?.amount,
              currency: transaction.balance?.currency,
            },
          };
        }
      }
    });

    return Object.values(currencies);
  }

  async signAll(event: Event) {
    event.preventDefault();

    const filterScheme: IFilterScheme = {
      paymentStatus: this.filterScheme.paymentStatus,
    };

    const historyPaymentsResult = await this.historyService.getHistoryList(
      this.paymentCategory,
      filterScheme,
      1,
      this.pageSizeForSign //    -   ,   
    );

    const historyPaymentsList = historyPaymentsResult.body;
    const signableList = historyPaymentsList.filter((payment) => {
      return this.historyService.isSignable(payment);
    });

    const modalParams: ISignModalParams = {
      transactionIds: this.retrieveIdsFromList(signableList),
      totalBalance: this.countTotalBalanceFromPaymentsList(signableList),
      transactionsCount: signableList.length,
    };
    if (this.filterScheme.paymentStatus === FILTER_PAYMENT_STATUS.DRAFT) {
      const validationStatus = await this.validateTransactionsByPaymentId(
        modalParams.transactionIds
      );
      if (validationStatus === BULK_OPERATION_STATUS.FAILED) {
        return;
      }
    }
    this.showSignModal(modalParams);
  }

  async sendAllForSign(event: Event) {
    event.preventDefault();

    const filterScheme: IFilterScheme = {
      paymentStatus: FILTER_PAYMENT_STATUS.DRAFT,
    };

    const historyPaymentsResult = await this.historyService.getHistoryList(
      this.paymentCategory,
      filterScheme,
      1,
      this.pageSizeForSign //    -   ,   
    );

    const historyPaymentsList = historyPaymentsResult.body;
    const sendableForSignList = historyPaymentsList.filter((payment) => {
      return this.historyService.isSendableForSign(payment);
    });

    this.sendToSigned(sendableForSignList);
  }

  async signSelected(event: Event) {
    event.preventDefault();
    const modalParams: ISignModalParams = {
      transactionIds: this.retrieveIdsFromList(this.selectedTransactions),
      totalBalance: this.countTotalBalanceFromTransactionsList(
        this.selectedTransactions
      ),
      transactionsCount: this.selectedTransactions.length,
    };
    if (this.filterScheme.paymentStatus === FILTER_PAYMENT_STATUS.DRAFT) {
      const validationStatus = await this.validateTransactionsByPaymentId(
        modalParams.transactionIds
      );
      if (validationStatus !== BULK_OPERATION_STATUS.SUCCESS) {
        return;
      }
    }
    this.showSignModal(modalParams);
  }

  async sendForSignSelected(event: Event) {
    event.preventDefault();
    this.sendToSigned(this.selectedTransactions);
  }

  async deleteSelectedTransactions(event: Event = null) {
    if (event) {
      event.preventDefault();
    }
    this.deleteTransactions(this.selectedTransactions as Transaction[]);
  }

  async exportSelectedTransactions(event: Event = null) {
    if (event) {
      event.preventDefault();
    }
    const exportedTransactions = await this.statementService.exportTransactions(
      this.selectedTransactions
    );
    const filename = `${this.selectedCount}_operations.pdf`;
    this.filesService.downloadFile(exportedTransactions, filename);
  }

  async onDelete(transaction: Transaction) {
    await this.deleteTransactions([transaction]);
  }

  async onRevoke(transaction: Transaction) {
    transaction.isLoading = true;

    try {
      await this.historyService.revokePayment(
        this.company.currentId,
        transaction
      );
      await this.init(this.filterScheme).toPromise();
    } catch (error) {
      this.notifierService.notify('error', error.description || error.code);
    } finally {
      this.historyService.updateForSignCount(null, this.paymentCategory);
      this.historyService.updateStatusCounts(this.paymentCategory);
      this.resetSelectable();
      this.updateSelectable();
    }
  }

  async deleteTransactions(transactions: Transaction[]) {
    transactions.forEach((transaction) => {
      transaction.isLoading = true;
    });

    try {
      const deleteResult = await this.historyService.deleteMultipleTransactions(
        this.company.currentId,
        transactions
      );
      await this.init(this.filterScheme).toPromise();

      const successCount = deleteResult.length;
      const failedCount = transactions.length - successCount;

      if (successCount === 0) {
        this.showDeleteError(transactions);
      } else if (failedCount === 0) {
        this.showDeleteSuccess(successCount);
      } else {
        this.showDeletePartial(successCount, failedCount);
      }
    } catch (error) {
      this.showDeleteError(transactions);
    } finally {
      this.historyService.updateForSignCount(null, this.paymentCategory);
      this.resetSelectable();
      this.updateSelectable();
    }
  }

  async sendToSigned(transactions: (Transaction | IHistoryPayment)[]) {
    try {
      const sendResult =
        await this.historyService.sendMultipleTransactionsForSign(
          transactions.map((transaction) => transaction.id)
        );
      switch (sendResult.status) {
        case BULK_OPERATION_STATUS.SUCCESS:
          this.showSendToSignedSuccess(sendResult);
          await this.init(this.filterScheme).toPromise();
          break;
        case BULK_OPERATION_STATUS.PARTLY_SUCCESS:
          this.showSendToSignedPartial(sendResult);
          await this.init(this.filterScheme).toPromise();
          break;
        case BULK_OPERATION_STATUS.FAILED:
          this.showSendToSignedPartialError(sendResult);
          break;
      }
    } catch (error) {
      console.error(error);
      this.showSendToSignedError(transactions);
    } finally {
      this.resetSelectable();
      this.updateSelectable();
      this.historyService.updateForSignCount(null, this.paymentCategory);
    }
  }

  async showSignModal(modalParams: ISignModalParams) {
    const modal = this.modalService.open(
      ConfirmSmsModalComponent,
      this.modalConfigMedium
    );

    Object.assign(modal.componentInstance, {
      additionalParams: {
        actionIds: modalParams.transactionIds,
      },
    });

    Object.assign(modal.componentInstance, {
      headerParams: {
        smsHeader: SMS_HEADERS.SIGNING_PAYMENT,
      },
    });

    (modal.componentInstance as ConfirmSmsModalComponent).submitted.subscribe(
      (smsToken: ISmsToken) => {
        this.signMultipleTransactions(smsToken, modalParams)
          .then(() => {
            localStorage.setItem(
              'preferredSignatureType',
              smsToken.type === TOKEN_TYPE.OTP
                ? CONFIRM_SIGNATURE_TYPE.OTP
                : CONFIRM_SIGNATURE_TYPE.SMS
            );
          })
          .catch((error) => {
            const errorText = error.description || error.code;
            this.showMultipleSignError(errorText, modalParams);
          })
          .finally(() => {
            this.historyService.updateForSignCount(null, this.paymentCategory);
            modal.close();
          });
      }
    );

    (
      modal.componentInstance as ConfirmSmsModalComponent
    ).sentForRevision.subscribe(() => {
      this.init(this.filterScheme).toPromise();
      modal.close();
    });
  }

  async signMultipleTransactions(
    smsToken: ISmsToken,
    modalParams: ISignModalParams
  ) {
    try {
      const signResult = await this.historyService.signMultipleTransactions(
        this.company.currentId,
        smsToken,
        modalParams.transactionIds
      );
      switch (signResult.status) {
        case BULK_OPERATION_STATUS.SUCCESS:
          this.showMultipleSignSuccess(signResult);
          await this.init(this.filterScheme).toPromise();
          break;
        case BULK_OPERATION_STATUS.PARTLY_SUCCESS:
          this.showMultipleSignPartialSuccess(signResult);
          await this.init(this.filterScheme).toPromise();
          break;
        case BULK_OPERATION_STATUS.FAILED:
          this.showMultipleSignPartialError(signResult);
          break;
      }
    } catch (error) {
      this.showMultipleSignError(error, modalParams);
    } finally {
      this.updateSelectable();
      this.historyService.updateForSignCount(null, this.paymentCategory);
    }
  }

  loadStatement() {
    this.isLoadingExtra = true;

    if (this.pageNumber === PAGE_START) {
      this.statement = null;
    }

    return this.historyService
      .getTransactionGroup(
        this.paymentCategory,
        this.filterScheme,
        this.pageNumber,
        this.filterScheme.paymentStatus === FILTER_PAYMENT_STATUS.FOR_SIGN
          ? this.pageSizeForSign
          : this.pageSize
      )
      .pipe(
        tap((retrievedTransactionGroup) => {
          this.hasMorePages =
            this.pageSize * this.pageNumber <=
            retrievedTransactionGroup.transactionsCount;

          this.statement = {
            totalTransactionsCount: retrievedTransactionGroup.transactionsCount,
            transactionGroups:
              this.filterScheme.paymentStatus === FILTER_PAYMENT_STATUS.FOR_SIGN
                ? this.destructureForSignGroups(retrievedTransactionGroup)
                : [retrievedTransactionGroup],
          };
        }),
        catchError((err) => {
          console.log(err);
          this.statement = null;
          return throwError(err);
        }),
        finalize(() => {
          this.isReady = true;
          this.isLoading = false;
          this.isLoadingExtra = false;
        })
      );
  }

  private destructureForSignGroups(mergedGroup: ITransactionGroup) {
    const forMeTransactions = mergedGroup.transactions.filter((transaction) => {
      return transaction.isSignable;
    });

    const forOthersTransactions = mergedGroup.transactions.filter(
      (transaction) => {
        return transaction.isSignableByOthers;
      }
    );

    const forMeToSign = {
      title: this.historyService.generateGroupTitle(
        FILTER_PAYMENT_STATUS.FOR_SIGN
      ),
      creditSum: null,
      debitSum: this.historyService.getTotalAmount(forMeTransactions),
      hasLargeTransactions:
        this.statementService.checkListForLargeTransactions(forMeTransactions),
      transactionsCount: forMeTransactions.length,
      transactions: forMeTransactions,
    };

    const forOthersToSign = {
      title: this.historyService.generateGroupTitle(
        FILTER_PAYMENT_STATUS.FOR_SIGN_OTHERS
      ),
      creditSum: null,
      debitSum: this.historyService.getTotalAmount(forOthersTransactions),
      hasLargeTransactions: this.statementService.checkListForLargeTransactions(
        forOthersTransactions
      ),
      transactionsCount: forOthersTransactions.length,
      transactions: forOthersTransactions,
    };

    return [forMeToSign, forOthersToSign];
  }

  private async showValidationError(validationResult: IBulkOperationResult) {
    const modal = this.modalService.open(
      ValidationErrorComponent,
      this.modalConfigMedium
    );
    validationResult.results = validationResult.results.map((item) => {
      item.documentNumber = this.getDocumentNumberById(item.itemId);
      return item;
    });
    modal.componentInstance.result = validationResult;
  }

  private async showMultipleSignPartialSuccess(
    signResult: IBulkOperationResult
  ) {
    const modal = this.modalService.open(
      MultipleSignPartialSuccessComponent,
      this.modalConfigMedium
    );

    signResult.results = signResult.results.map((signItem) => {
      signItem.documentNumber = this.getDocumentNumberById(signItem.itemId);
      return signItem;
    });

    modal.componentInstance.result = signResult;
  }

  private async showMultipleSignPartialError(signResult: IBulkOperationResult) {
    const modal = this.modalService.open(
      MultipleSignPartialErrorComponent,
      this.modalConfigMedium
    );
    signResult.results = signResult.results.map((signItem) => {
      signItem.documentNumber = this.getDocumentNumberById(signItem.itemId);
      return signItem;
    });
    modal.componentInstance.signResult = signResult;
  }

  private async showMultipleSignSuccess(result: IBulkOperationResult) {
    const modal = this.modalService.open(
      MultipleSignSuccessComponent,
      this.modalConfigSmall
    );

    modal.componentInstance.result = result;
  }

  private async showMultipleSignError(
    errorText: string = null,
    modalParams: ISignModalParams
  ) {
    const modal = this.modalService.open(
      MultipleSignErrorComponent,
      this.modalConfigSmall
    );

    modal.componentInstance.errorText = errorText;

    try {
      await modal.result;
      this.showSignModal(modalParams);
    } catch (error) {}
  }

  private async showDeleteSuccess(successCount: number) {
    const modal = this.modalService.open(
      DeleteSuccessComponent,
      this.modalConfigSmall
    );
    modal.componentInstance.successCount = successCount;
  }

  private async showDeletePartial(successCount: number, failedCount: number) {
    const modal = this.modalService.open(
      DeletePartialComponent,
      this.modalConfigMedium
    );
    modal.componentInstance.successCount = successCount;
    modal.componentInstance.failedCount = failedCount;
  }

  private async showDeleteError(deletingTransactions: Transaction[]) {
    const modal = this.modalService.open(
      DeleteErrorComponent,
      this.modalConfigSmall
    );
    modal.componentInstance.failedCount = deletingTransactions.length;
    try {
      await modal.result;
      this.deleteTransactions(deletingTransactions);
    } catch (error) {}
  }

  private async showSendToSignedSuccess(signResult: IBulkOperationResult) {
    const modal = this.modalService.open(
      SendToSignedSuccessComponent,
      this.modalConfigSmall
    );

    (modal.componentInstance as SendToSignedErrorComponent).signResult =
      signResult;

    // modal.componentInstance.successCount = successCount;
  }

  private async showSendToSignedPartial(signResult: IBulkOperationResult) {
    const modal = this.modalService.open(
      SendToSignedPartialComponent,
      this.modalConfigMedium
    );

    signResult.results = signResult.results.map((signItem) => {
      signItem.documentNumber = this.getDocumentNumberById(signItem.itemId);
      return signItem;
    });

    (modal.componentInstance as SendToSignedErrorComponent).signResult =
      signResult;

    // modal.componentInstance.successCount = successCount;
    // modal.componentInstance.failedCount = failedCount;
  }

  private async showSendToSignedPartialError(signResult: IBulkOperationResult) {
    const modal = this.modalService.open(
      SendToSignedPartialErrorComponent,
      this.modalConfigMedium
    );

    signResult.results = signResult.results.map((signItem) => {
      signItem.documentNumber = this.getDocumentNumberById(signItem.itemId);
      return signItem;
    });

    (modal.componentInstance as SendToSignedPartialErrorComponent).signResult =
      signResult;

    // modal.componentInstance.successCount = successCount;
    // modal.componentInstance.failedCount = failedCount;
  }

  private async showSendToSignedError(
    sendingTransactions: (Transaction | IHistoryPayment)[]
  ) {
    const modal = this.modalService.open(
      SendToSignedErrorComponent,
      this.modalConfigSmall
    );

    (modal.componentInstance as SendToSignedErrorComponent).signResult = {
      numberOfFailed: sendingTransactions.length,
      numberOfSuccess: 0,
      status: BULK_OPERATION_STATUS.FAILED,
    };

    try {
      await modal.result;
      this.sendToSigned(sendingTransactions);
    } catch (error) {}
  }

  private resetSelectable() {
    this.selectedTransactions = [];
    this.selectedCount = 0;
    this.selectedBalance = {
      amount: 0,
      currency: null,
    };

    this.isAllSelected = false;
    this.hasSignableSelected = false;
  }

  private updateSelectable() {
    this.resetSelectable();
    this.updateSelectedValues();
  }

  private retrieveIdsFromList(list) {
    return list.map((item) => {
      return item.id;
    });
  }

  private updateSelectedValues() {
    this.currentTransactions.forEach((item) => {
      if (item.isSelected) {
        this.selectedTransactions.push(item);
        this.selectedCount++;
      }
    });

    this.selectedBalance = this.countTotalBalanceFromTransactionsList(
      this.selectedTransactions
    );
  }

  private countTotalBalanceFromPaymentsList(payments: IHistoryPayment[]) {
    const result = {
      amount: 0,
      currency: null,
    };

    payments.forEach((payment) => {
      if (!result.currency) {
        result.currency = payment.details.paymentAmount?.currency;
      }
      result.amount += payment.details.paymentAmount?.amount;
    });

    return result;
  }

  private countTotalBalanceFromTransactionsList(transactions: Transaction[]) {
    const result = {
      amount: 0,
      currency: null,
    };

    transactions.forEach((item) => {
      if (item.debit) {
        if (!result.currency && item.debit.currency) {
          result.currency = item.debit.currency;
        }
        result.amount += item.debit.amount;
      }
    });

    return result;
  }

  private isAllSignableInList(transactions: Transaction[]) {
    let result = true;
    transactions.forEach((transaction) => {
      if (result === false) {
        return result;
      }

      if (!transaction.isSignable) {
        result = false;
      }
    });

    return result;
  }

  private isAllSendableForSignInList(transactions: Transaction[]) {
    let result = true;
    transactions.forEach((transaction) => {
      if (result === false) {
        return result;
      }

      if (!transaction.isSendableForSign) {
        result = false;
      }
    });

    return result;
  }

  private hasSignableInList(transactions: Transaction[]) {
    return transactions.find((transaction) => {
      return transaction.isSignable;
    })
      ? true
      : false;
  }

  private hasSendableForSignInList(transactions: Transaction[]) {
    return transactions.find((transaction) => {
      return transaction.isSendableForSign;
    })
      ? true
      : false;
  }

  private mapQueryParams(queryParams: any) {
    let filter = Object.assign(
      {},
      this.filterService.getDefaults(this.filterMode)
    );

    for (const key in queryParams) {
      if (Object.prototype.hasOwnProperty.call(queryParams, key)) {
        const param = queryParams[key];

        if (!param) {
          continue;
        }

        if (key == 'periodFrom' || key == 'periodTo') {
          filter[key] = new Date(param);
        } else {
          filter[key] = param;
        }
      }
    }

    return filter;
  }

  private updateQueryParams() {
    const queryParams: any = Object.assign({}, this.filterScheme);

    const newFilter = {};

    for (const key in queryParams) {
      if (Object.prototype.hasOwnProperty.call(queryParams, key)) {
        const param = queryParams[key];

        if (param && param != null && param != 'null') {
          if (key == 'periodFrom' || key == 'periodTo') {
            newFilter[key] = this.datePipe.transform(param, 'yyyy-MM-dd');
          } else {
            newFilter[key] = param;
          }

          if (key == 'amountFrom' || key == 'amountTo') {
            newFilter[key] = this.maskedService.getNumberedAmount(param);
          }
        }
      }
    }

    this.updateCashedRouteQuery(newFilter);
    this.router.navigate([], {
      queryParams: newFilter,
      relativeTo: this.route,
      skipLocationChange: false,
    });
  }

  private updateCashedRouteQuery(queryParams) {
    sessionStorage.setItem(this.SESSION_ROUTE_KEY, JSON.stringify(queryParams));
  }

  private getDocumentNumberById(transactionId: string) {
    const found = this.currentTransactions.find((transaction) => {
      return transaction.id == transactionId;
    });
    return (found && found.documentNumber) || null;
  }
}
