import { Component, Input, OnInit } from '@angular/core';
import { NgbActiveModal } from '@ng-bootstrap/ng-bootstrap';
import { BULK_OPERATION_STATUS, IBulkOperationItem, IBulkOperationResult } from '@shared/models';

@Component({
  selector: 'j-send-to-signed-partial',
  templateUrl: './send-to-signed-partial.component.html',
  styleUrls: ['./send-to-signed-partial.component.scss'],
})
export class SendToSignedPartialComponent {
  @Input() signResult: IBulkOperationResult;

  constructor(public modal: NgbActiveModal) {}

  dismissModal() {
    this.modal.dismiss();
  }

  closeModal() {
    this.modal.close();
  }

  retrieveErrors(transaction: IBulkOperationItem) {
    let mappedErrors;

    if (transaction.errorData) {
      const errors = Object.entries(transaction.errorData);
      mappedErrors = errors.map((item) => {
        return item[1];
      });
    } else if (transaction.errorMsg) {
      mappedErrors = [transaction.errorMsg];
    } else {
      mappedErrors = [];
    }

    return mappedErrors;
  }

  get failedCount() {
    return this.signResult?.numberOfFailed || 0;
  }

  get successCount() {
    return this.signResult?.numberOfSuccess || 0;
  }

  get failedText() {
    switch (this.failedCount % 20) {
      case 1:
        return 'HISTORY.SEND_TO_SIGNED.FAILED.ONE';
      case 2:
      case 3:
      case 4:
        return 'HISTORY.SEND_TO_SIGNED.FAILED.FEW';
      default:
        return 'HISTORY.SEND_TO_SIGNED.FAILED.OTHER';
    }
  }

  get failedParams() {
    return {
      transactionsCount: this.failedCount,
    };
  }

  get failedTransactions() {
    return (
      this.signResult &&
      this.signResult.results &&
      this.signResult.results.filter((signItem) => {
        return signItem.status === BULK_OPERATION_STATUS.FAILED;
      })
    );
  }

  get successText() {
    switch (this.successCount % 10) {
      case 1:
        return 'HISTORY.SEND_TO_SIGNED.SUCCESS.ONE';
      case 2:
      case 3:
      case 4:
        return 'HISTORY.SEND_TO_SIGNED.SUCCESS.FEW';
      default:
        return 'HISTORY.SEND_TO_SIGNED.SUCCESS.OTHER';
    }
  }

  get successParams() {
    return {
      transactionsCount: this.successCount,
    };
  }
}
