import { Injectable } from '@angular/core';
import { HttpParams, HttpHeaders, HttpResponse } from '@angular/common/http';

import { TranslateService } from '@ngx-translate/core';

import { ApiService, HEADERS } from '@api/index';

import {
  IFilterScheme,
  FILTER_PAYMENT_STATUS,
  FILTER_SORT,
} from '@filter/filter.model';

import { Transaction, ITransactionGroup } from '@statement/index';
import { StatementService } from '@statement/statement.service';

import { convertDateToApiFormat } from '@utils/index';

import { CompanyService } from '@company/company.service';

import { FilterService } from '@filter/filter.service';

import { FeatureFlagsService, FEATURE_FLAG } from '@feature-flags/index';

import { IMoney } from '@shared/currency/currency.model';
import { MaskedService } from '@shared/masked/masked.service';
import { CurSymbolPipe } from '@shared/pipes';
import {
  PAYMENT_STATUS,
  PAYMENT_TYPE,
  ISmsToken,
  TOKEN_TYPE,
  PAYMENT_CATEGORY,
  IHistoryPayment,
  IBulkOperationResult,
  HISTORY_PAYMENT_STATE,
} from '@shared/models';
import { Observable, of } from 'rxjs';
import { map } from 'rxjs/operators';

export const MAX_PAGE_SIZE_FOR_SIGN = 100000;
export interface IBadge {
  statusCode: PAYMENT_STATUS;
  count: number;
}

export interface IPaymentsSignAllRequest {
  itemIds: string[];
}

@Injectable({
  providedIn: 'root',
})
export class HistoryService {
  private cashedCategory: PAYMENT_CATEGORY;
  private cashedFilter: IFilterScheme;
  private cashedTransactionGroup: ITransactionGroup;
  private cashedPageNumber: number;
  private cashedPageSize: number;

  constructor(
    private statementService: StatementService,
    private companyService: CompanyService,
    private api: ApiService,
    private translateService: TranslateService,
    private filterService: FilterService,
    private maskedService: MaskedService,
    private curSymbolPipe: CurSymbolPipe,
    private featureFlagsService: FeatureFlagsService
  ) {}

  getTransactionGroup(
    category: PAYMENT_CATEGORY = PAYMENT_CATEGORY.DOMESTIC,
    filter: IFilterScheme,
    pageNumber: number = 1,
    pageSize: number = 50
  ): Observable<ITransactionGroup> {
    if (
      //     interceptor-a 
      this.cashedCategory === category &&
      this.cashedTransactionGroup &&
      this.cashedFilter === filter &&
      this.cashedPageNumber === pageNumber &&
      this.cashedPageSize === pageSize
    ) {
      return of(this.cashedTransactionGroup);
    }

    const isNextPage =
      this.cashedFilter === filter && pageNumber > this.cashedPageNumber;
    return this.getHistoryListObservable(
      category,
      filter,
      pageNumber,
      pageSize
    ).pipe(
      map((historyPaymentsResult) => {
        this.cashedCategory = category;
        this.cashedFilter = filter;
        this.cashedPageNumber = pageNumber;

        this.cashedPageSize = pageSize;
        const historyPaymentsList = historyPaymentsResult.body;
        const mappedTransactions = this.mapHistoryPayments(historyPaymentsList);

        let mergedTransactions = isNextPage
          ? this.cashedTransactionGroup.transactions.concat(mappedTransactions)
          : mappedTransactions;

        if (filter.paymentStatus === FILTER_PAYMENT_STATUS.FOR_SIGN) {
          mergedTransactions = mergedTransactions.sort(this.compareBySignature);
          mergedTransactions = mergedTransactions.sort(this.compareByDate);
        }

        if (filter.paymentStatus === FILTER_PAYMENT_STATUS.FAILED) {
          this.markFailedAsRead();
        }

        this.cashedTransactionGroup = {
          title: this.generateGroupTitle(filter.paymentStatus),
          creditSum: null,
          debitSum: this.getTotalAmount(mergedTransactions),
          hasLargeTransactions:
            this.statementService.checkListForLargeTransactions(
              mergedTransactions
            ),
          transactionsCount: mergedTransactions.length,
          transactions: mergedTransactions,
        };

        return this.cashedTransactionGroup;
      })
    );
  }

  async getHistoryList(
    category: PAYMENT_CATEGORY,
    filter: IFilterScheme,
    pageNumber: number = 1,
    pageSize: number = 50
  ) {
    const url = `companies/${this.companyService.currentId}/payments`;
    const headers = new HttpHeaders();

    let params = new HttpParams();

    params = this.appendCategoryParams(category, params);
    params = params.append('page', `${pageNumber}`);
    params = params.append('pageSize', `${pageSize}`);
    params = this.appendFilterParams(filter, params);

    return this.api.get<HttpResponse<IHistoryPayment[]>>(url, headers, params, {
      observe: 'response',
    });
  }

  getHistoryListObservable(
    category: PAYMENT_CATEGORY,
    filter: IFilterScheme,
    pageNumber: number = 1,
    pageSize: number = 50
  ): Observable<HttpResponse<IHistoryPayment[]>> {
    const url = `/companies/${this.companyService.currentId}/payments`;
    const headers = new HttpHeaders();

    let params = new HttpParams();

    params = this.appendCategoryParams(category, params);
    params = params.append('page', `${pageNumber}`);
    params = params.append('pageSize', `${pageSize}`);
    params = this.appendFilterParams(filter, params);

    const extras = {
      observe: 'response',
    };

    return this.api.getObservable<HttpResponse<IHistoryPayment[]>>(
      url,
      headers,
      params,
      extras
    );
  }

  sendMultipleTransactionsForSign(transactionIds: string[]) {
    const url = `companies/${this.companyService.currentId}/payments:to-created`;
    let params = new HttpParams();
    transactionIds.forEach((id) => {
      params = params.append('paymentId', id);
    });

    return this.api.patch<IBulkOperationResult>(url, null, null, params);
  }

  async validateTransactionsByPaymentId(
    companyId: string,
    transactionIds: string[]
  ) {
    const url = `companies/${companyId}/payments/signatures/validate`;
    let params = new HttpParams();
    transactionIds.forEach((id) => {
      params = params.append('paymentId', id);
    });
    const headers = new HttpHeaders().append(
      'Content-Type',
      'application/x-www-form-urlencoded;charset=UTF-8'
    );
    return this.api.post<IBulkOperationResult>(url, params, headers);
  }

  async signMultipleTransactions(
    companyId: string,
    smsToken: ISmsToken,
    transactionIds: string[]
  ) {
    const url = `companies/${companyId}/payments/signatures`;
    let headers = new HttpHeaders();

    if (smsToken.type === TOKEN_TYPE.OTP) {
      headers = headers.set(HEADERS.OTP, smsToken.signature);
    } else {
      headers = headers.set(HEADERS.PHONE, smsToken.signature);
    }

    const body: IPaymentsSignAllRequest = { itemIds: transactionIds };

    return this.api.post<IBulkOperationResult>(url, body, headers);
  }

  async deleteTransaction(companyId: string, transaction: Transaction) {
    const url = `companies/${companyId}/payments`;
    const headers = new HttpHeaders();

    let params = new HttpParams().set('id', transaction.id);

    return this.api.delete<string[]>(url, headers, params);
  }

  async revokePayment(companyId: string, transaction: Transaction) {
    const url = `companies/${companyId}/payments/${transaction.paymentId}:to-revoked`;
    return this.api.patch<string[]>(url);
  }

  async deleteMultipleTransactions(
    companyId: string,
    transactions: Transaction[]
  ) {
    const url = `companies/${companyId}/payments`;
    const headers = new HttpHeaders();

    let params = new HttpParams();
    transactions.forEach((transaction) => {
      params = params.append('id', transaction.id);
    });

    return this.api.delete<string[]>(url, headers, params);
  }

  public isRemovable(payment: IHistoryPayment) {
    return (
      payment.status.code === PAYMENT_STATUS.CREATED ||
      payment.status.code === PAYMENT_STATUS.DRAFT
    );
  }

  public isEditable(payment: IHistoryPayment) {
    if (
      payment.type === PAYMENT_TYPE.CONVERSION &&
      payment.status.code !== PAYMENT_STATUS.CREATED
    ) {
      return false;
    }

    return (
      payment.status.code === PAYMENT_STATUS.DRAFT ||
      payment.status.code === PAYMENT_STATUS.CREATED
    );
  }

  public isRevokable(payment: IHistoryPayment) {
    return payment.status.code === PAYMENT_STATUS.READY_TO_SEND;
  }

  public getSignsCountText(transaction: Transaction) {
    const signed =
      (transaction.signatures && transaction.signatures.length) || 0;
    const total = this.companyService.isMultipleSignScheme(
      transaction.signatureScheme
    )
      ? 2
      : 1;
    return `${signed}/${total}`;
  }

  private appendCategoryParams(category: PAYMENT_CATEGORY, params: HttpParams) {
    switch (category) {
      case PAYMENT_CATEGORY.ALL:
        params = params.append('category', PAYMENT_CATEGORY.DOMESTIC);
        params = params.append('category', PAYMENT_CATEGORY.FOREIGN);
        params = params.append('category', PAYMENT_CATEGORY.CONVERSION);
        break;

      default:
        params = params.append('category', category);
        break;
    }
    return params;
  }

  private appendFilterParams(filter: IFilterScheme, params: HttpParams) {
    params = this.appendStatusToParams(filter.paymentStatus, params);

    if (filter.documentNumber) {
      params = params.append('documentNumber', filter.documentNumber);
    }

    if (filter.periodFrom) {
      params = params.append(
        'dateFrom',
        convertDateToApiFormat(filter.periodFrom)
      );
    }

    if (filter.periodTo) {
      params = params.append('dateTo', convertDateToApiFormat(filter.periodTo));
    }

    if (filter.amountFrom) {
      params = params.append('amountFrom', `${filter.amountFrom}`);
    }

    if (filter.amountTo) {
      params = params.append('amountTo', `${filter.amountTo}`);
    }

    if (filter.recipientIban) {
      params = params.append('correspondentAccount', filter.recipientIban);
    }

    if (filter.recipientBin) {
      params = params.append('correspondentBin', filter.recipientBin);
    }

    if (filter.type) {
      params = params.append('type', filter.type);
    }

    switch (filter.sortBy) {
      case FILTER_SORT.AMOUNT_ASC:
        params = params.append('sortBy', 'AMOUNT');
        break;

      case FILTER_SORT.AMOUNT_DESC:
        params = params.append('sortBy', '-AMOUNT');
        break;
    }

    return params;
  }

  private appendStatusToParams(
    filterStatus: FILTER_PAYMENT_STATUS,
    params: HttpParams
  ) {
    switch (filterStatus) {
      case FILTER_PAYMENT_STATUS.ALL:
        params = params.append('status', PAYMENT_STATUS.DRAFT);

        params = params.append('status', PAYMENT_STATUS.CREATED);

        params = params.append('status', PAYMENT_STATUS.REJECTED);

        params = params.append('status', PAYMENT_STATUS.BANK_COMPLETED);

        params = params.append('status', PAYMENT_STATUS.BANK_PROCESSING);
        params = params.append('status', PAYMENT_STATUS.BANK_SENT);
        params = params.append('status', PAYMENT_STATUS.READY_TO_SEND);
        params = params.append('status', PAYMENT_STATUS.IN_BANK);
        break;

      case FILTER_PAYMENT_STATUS.DRAFT:
        params = params.append('status', PAYMENT_STATUS.DRAFT);
        break;

      case FILTER_PAYMENT_STATUS.FOR_SIGN:
        params = params.append('status', PAYMENT_STATUS.CREATED);
        break;

      case FILTER_PAYMENT_STATUS.FAILED:
        params = params.append('status', PAYMENT_STATUS.REJECTED);
        break;

      case FILTER_PAYMENT_STATUS.SUCCESS:
        params = params.append('status', PAYMENT_STATUS.BANK_COMPLETED);
        break;

      case FILTER_PAYMENT_STATUS.IN_PROGRESS:
        params = params.append('status', PAYMENT_STATUS.BANK_PROCESSING);
        params = params.append('status', PAYMENT_STATUS.BANK_SENT);
        params = params.append('status', PAYMENT_STATUS.READY_TO_SEND);
        params = params.append('status', PAYMENT_STATUS.IN_BANK);
        break;
    }
    return params;
  }

  private compareByDate(a: Transaction, b: Transaction) {
    if (a.paymentStatus.timestamp < b.paymentStatus.timestamp) {
      return 1;
    }
    if (a.paymentStatus.timestamp > b.paymentStatus.timestamp) {
      return -1;
    }
    return 0;
  }

  async updateForSignCount(
    payments: IHistoryPayment[] = null,
    paymentCategory: PAYMENT_CATEGORY
  ) {
    this.filterService.updateForSignCount(null);
    if (!payments) {
      const result = await this.retrivePaymentsByStatus(
        FILTER_PAYMENT_STATUS.FOR_SIGN,
        paymentCategory
      );
      payments = result.payments;
    }

    let forSignMeCount = 0;
    let forSignOthersCount = 0;

    payments?.forEach((payment) => {
      if (
        this.companyService.isSignable(
          payment.status.code,
          payment.signatureScheme,
          this.companyService.currentAuthority.authority,
          payment.signatures?.length
        )
      ) {
        forSignMeCount++;
      } else {
        forSignOthersCount++;
      }
    });

    if (forSignMeCount == 0 && forSignOthersCount == 0) {
      this.filterService.updateForSignCount(0);
    } else if (forSignMeCount > 0) {
      this.filterService.updateForSignCount(forSignMeCount, true);
    } else {
      this.filterService.updateForSignCount(forSignOthersCount);
    }
  }

  private async retrivePaymentsByStatus(
    paymentStatus: FILTER_PAYMENT_STATUS,
    paymentCategory: PAYMENT_CATEGORY
  ) {
    let retrievedCount = 0;
    let payments = null;

    const url = `companies/${this.companyService.currentId}/payments`;
    const headers = new HttpHeaders();

    let params = new HttpParams();
    params = this.appendStatusToParams(paymentStatus, params);
    params = this.appendCategoryParams(paymentCategory, params);
    params = params.append('pageSize', `${MAX_PAGE_SIZE_FOR_SIGN}`);

    try {
      const historyPaymentsResult = await this.api.get<
        HttpResponse<IHistoryPayment[]>
      >(url, headers, params, {
        observe: 'response',
      });
      payments = historyPaymentsResult.body;
      retrievedCount = +historyPaymentsResult.headers.get('X-Total-Count');
    } catch (error) {
    } finally {
      return {
        payments: payments,
        count: retrievedCount,
      };
    }
  }

  updateStatusCounts(paymentCategory: PAYMENT_CATEGORY) {
    this.updateForSignCount(null, paymentCategory);
    this.updateCountsByBadges(paymentCategory);
  }

  public isSignable(payment: IHistoryPayment) {
    const hasSignRights = this.companyService.isSignable(
      payment.status.code,
      payment.signatureScheme,
      this.companyService.currentAuthority.authority,
      payment.signatures?.length
    );

    if (payment.status.code === PAYMENT_STATUS.DRAFT) {
      return hasSignRights && this.isCorrectlyFullfilled(payment);
    }

    return hasSignRights;
  }

  public isSignableByOthers(payment: IHistoryPayment) {
    return this.companyService.isSignableByOthers(
      payment.status.code,
      payment.signatureScheme,
      this.companyService.currentAuthority.authority,
      payment.signatures?.length
    );
  }

  public isSendableForSign(payment: IHistoryPayment) {
    const isSignableByOthers = this.isSignableByOthers(payment);
    const isSendableForSign = this.companyService.isSendableForSign(
      payment.status.code,
      payment.signatureScheme,
      this.companyService.currentAuthority.authority
    );

    return !isSignableByOthers && isSendableForSign;
  }

  private isCorrectlyFullfilled(payment: IHistoryPayment) {
    // TODO: [JSN-1755] Make this check)
    return true;
  }

  private async markFailedAsRead() {
    const url = `companies/${this.companyService.currentId}/payments/badges:mark-read-rejected`;

    try {
      await this.api.put(url);
    } catch (error) {
    } finally {
      this.filterService.updateFailedCount(0);
    }
  }

  private async updateCountsByBadges(paymentCategory: PAYMENT_CATEGORY) {
    this.filterService.updateInProgressCount(null);
    this.filterService.updateFailedCount(null);

    let inProgressCount = 0;
    let failedCount = 0;
    const url = `companies/${this.companyService.currentId}/payments/badges`;

    let params = new HttpParams();
    params = this.appendCategoryParams(paymentCategory, params);

    try {
      const badges = await this.api.get<IBadge[]>(url, null, params);
      badges.forEach((badge) => {
        switch (badge.statusCode) {
          case PAYMENT_STATUS.READY_TO_SEND:
          case PAYMENT_STATUS.IN_BANK:
          case PAYMENT_STATUS.BANK_PROCESSING:
          case PAYMENT_STATUS.BANK_SENT:
            inProgressCount += badge.count;
            break;
          case PAYMENT_STATUS.REJECTED:
            failedCount += badge.count;
            break;
        }
      });
    } catch (error) {
    } finally {
      this.filterService.updateInProgressCount(inProgressCount);
      this.filterService.updateFailedCount(failedCount);
    }
  }

  getTotalAmount(transactions: Transaction[]): IMoney {
    let total = 0;
    let currency = null;
    transactions.forEach((item) => {
      if (item.balance) {
        if (item.balance.currency && !currency) {
          currency = item.balance.currency;
        }
        total += item.balance.amount;
      }
    });
    return {
      currency: currency,
      amount: total,
    };
  }

  private mapDescription(payment: IHistoryPayment) {
    switch (payment.type) {
      case PAYMENT_TYPE.CONVERSION:
        const amount = this.maskedService.getMaskedAmount(
          payment.details.exchangeRate?.calculatedSum
        );
        const currency = this.curSymbolPipe.transform(
          payment.details.exchangeRate?.currencyTo
        );

        let mainDescription;
        if (amount && currency) {
          mainDescription = this.translateService.instant(
            'CONVERSION.DESCRIPTION',
            {
              money: `${amount} ${currency}`,
              iban: payment.paymentRecipient.recipientAccount.iban,
            }
          );
        } else {
          mainDescription = payment.details?.description || null;
        }

        if (mainDescription) {
          return `${mainDescription} ${
            payment.details.conversionPurpose?.description || ''
          }`;
        } else if (payment.details.conversionPurpose?.description) {
          return `${payment.details.conversionPurpose?.description}`;
        }
        return null;

      default:
        return payment.details?.description || null;
    }
  }

  private mapHistoryPayments(payments: IHistoryPayment[]): Transaction[] {
    return payments.map((payment, index) => {
      const transaction = {
        id: payment.id || null,
        iban: payment.details?.payerIban || null,
        reference: payment.id || null,
        documentNumber: payment.details?.documentId || null,
        correspondentBankBic:
          payment.paymentRecipient?.recipientAccount?.bic || null,
        correspondentAccount:
          payment.paymentRecipient?.recipientAccount?.iban || null,
        correspondentName: payment.paymentRecipient?.name || null,
        kbe: payment.paymentRecipient?.kbe || null,
        correspondentBin: payment.paymentRecipient?.iinOrBin || null,
        debit: payment.details?.paymentAmount || null,
        credit: null,
        description: this.mapDescription(payment),
        documentDate: payment.details?.issueDate || null,
        valueDate: payment.details?.valueDate || null,
        knp: payment.details?.knp || null,
        kbk: payment.details?.kbk || null,
        signHead: null,
        signBuch: null,
        isPayment: true,
        paymentId: payment.id,
        paymentStatus: payment.status || null,
        paymentType: payment.type || null,
        paymentEmployees: payment.paymentEmployees,
        createDate: payment.details?.createDate || null,
        foreignPaymentDetails: payment.details.foreignPaymentDetails || null,
        foreignRecipient: {
          country: payment.paymentRecipient?.country,
          countryCode: payment.paymentRecipient?.countryCode,
          cityName: payment.paymentRecipient?.cityName,
          bic: payment.paymentRecipient?.recipientAccount?.bic,
          bankName: payment.paymentRecipient?.recipientAccount?.bankName,
        },

        emailOnSuccess: payment.emailOnSuccess || null,

        signatureScheme: payment.signatureScheme || null,
        signatures: payment.signatures || [],
      };

      const newTransaction = new Transaction(
        transaction,
        this.translateService
      );

      if (this.isFeatureDisabled(payment.type)) {
        newTransaction.isExportable = false;
        newTransaction.isRepeatable = false;
        newTransaction.isEditable = false;
        newTransaction.isRemovable = false;
        newTransaction.isSignable = false;
        newTransaction.isSendableForSign = false;
        newTransaction.isRevokable = false;
      } else {
        newTransaction.isExportable = this.isExportable(payment);
        newTransaction.isRepeatable = this.isRepeatable(payment);
        newTransaction.isEditable = this.isEditable(payment);
        newTransaction.isRemovable = this.isRemovable(payment);
        newTransaction.isSignable = this.isSignable(payment);
        newTransaction.isSendableForSign = this.isSendableForSign(payment);
        newTransaction.isRevokable = this.isRevokable(payment);
      }

      newTransaction.associatedField =
        payment.details?.associatedField?.parameters;

      newTransaction.isSignableByOthers = this.isSignableByOthers(payment);
      newTransaction.signsCountText = this.getSignsCountText(newTransaction);

      return newTransaction;
    });
  }

  private isFeatureDisabled(paymentType: PAYMENT_TYPE) {
    const isTypeConversionAndDisabled =
      paymentType == PAYMENT_TYPE.CONVERSION &&
      this.featureFlagsService.isDisabled(FEATURE_FLAG.CONVERSION);
    const isTypeForeignAndDisabled =
      paymentType == PAYMENT_TYPE.FOREIGN &&
      this.featureFlagsService.isDisabled(FEATURE_FLAG.FOREIGN_PAYMENT);

    return isTypeConversionAndDisabled || isTypeForeignAndDisabled || false;
  }

  private isRepeatable(payment: IHistoryPayment) {
    if (
      (payment.status.state == HISTORY_PAYMENT_STATE.MIGRATED &&
        payment.category === PAYMENT_CATEGORY.CONVERSION) ||
      payment.status.code == PAYMENT_STATUS.DRAFT
    ) {
      return false;
    } else {
      return true;
    }
  }

  private isExportable(payment: IHistoryPayment) {
    if (
      (payment.status.state === HISTORY_PAYMENT_STATE.MIGRATED &&
        payment.category === PAYMENT_CATEGORY.CONVERSION) ||
      payment.status.code === PAYMENT_STATUS.CREATED
    ) {
      return false;
    } else {
      return true;
    }
  }

  generateGroupTitle(filterStatus: FILTER_PAYMENT_STATUS) {
    switch (filterStatus) {
      case FILTER_PAYMENT_STATUS.ALL:
        return 'HISTORY.LISTS.ALL';
      case FILTER_PAYMENT_STATUS.FOR_SIGN:
        return 'HISTORY.LISTS.FOR_SIGN';
      case FILTER_PAYMENT_STATUS.FOR_SIGN_OTHERS:
        return 'HISTORY.LISTS.FOR_SIGN_OTHERS';
      case FILTER_PAYMENT_STATUS.SUCCESS:
        return 'HISTORY.LISTS.SUCCESS';
      case FILTER_PAYMENT_STATUS.FAILED:
        return 'HISTORY.LISTS.FAILED';
      case FILTER_PAYMENT_STATUS.IN_PROGRESS:
        return 'HISTORY.LISTS.IN_PROGRESS';
      case FILTER_PAYMENT_STATUS.DRAFT:
        return 'HISTORY.LISTS.DRAFT';
    }
  }

  private compareBySignature(a: Transaction, b: Transaction) {
    if (a.isSignable && b.isSignable) {
      return 0;
    } else if (a.isSignable && b.isSignableByOthers) {
      return -1;
    } else {
      return 1;
    }
  }
}
