import { Component, Input } from '@angular/core';
import { NgbActiveModal } from '@ng-bootstrap/ng-bootstrap';

@Component({
  selector: 'j-multiple-sign-error',
  templateUrl: './multiple-sign-error.component.html',
  styleUrls: ['./multiple-sign-error.component.scss'],
})
export class MultipleSignErrorComponent {
  @Input() errorText: string;

  constructor(public modal: NgbActiveModal) {}

  dismissModal() {
    this.modal.dismiss();
  }

  closeModal() {
    this.modal.close();
  }
}
